import React from 'react';
import { withStyles, makeStyles, Grid, Typography, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Button } from '@material-ui/core';

const TableContainerGrid = withStyles({
  root: {
    padding: 20,
    borderRadius: 10,
  }
})(Grid)

const TablePaper = withStyles({
  root: {
    background: 'transparent',
    boxShadow: 'none',
  }
})(Paper)

const useStyles = makeStyles({
  table: {
  },
  borderNone: {
    border: 'none',
    textAlign: 'center',
  },
  flexCenter: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  coinLink: {
    marginTop: 10,
    fontSize: 12,
    justifyContent: 'flex-end',
    display: 'flex',
    alignItems: 'flex-end',
    width: '100%',
  }
});

const CustomTableContainer = ({title, colorBackground, rows, spacing = {}}) => {
  const classes = useStyles();

  return (
    <React.Fragment>
      <TableContainerGrid container style={{background: colorBackground, marginTop: spacing !== {} ? spacing : 0}}>
        <TableContainer component={TablePaper}>
          <Typography>{title}</Typography>
          <Table className={classes.table} aria-label="simple table">
            <TableHead className={classes.borderNone}>
              <TableRow  className={classes.borderNone}>
                <TableCell className={classes.borderNone}></TableCell>
                <TableCell align="right" className={classes.borderNone}></TableCell>
                <TableCell align="right" className={classes.borderNone}>Holders</TableCell>
                <TableCell align="right" className={classes.borderNone}>Market Cap</TableCell>
                <TableCell align="right" className={classes.borderNone}></TableCell>
              </TableRow>
            </TableHead>
            <TableBody className={classes.borderNone}>
              {rows.map((row, key) => (
                <TableRow key={key} className={classes.borderNone}>
                  <TableCell component="th" scope="row" className={`${classes.flexCenter} ${classes.borderNone}`}>
                    <img width={36} src={row.image}  alt="Avatar"/>
                  </TableCell>
                  <TableCell align="right" className={classes.borderNone}>{row.calories}</TableCell>
                  <TableCell align="right" className={classes.borderNone}>{row.fat}</TableCell>
                  <TableCell align="right" className={classes.borderNone}>{row.carbs}</TableCell>
                  <TableCell align="right" className={classes.borderNone}>
                    <Button>
                      <img width={20} src={row.imgSend}  alt="Send"/>
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        <Typography className={classes.coinLink}>Feature Your Coin Here</Typography>
      </TableContainerGrid>
    </React.Fragment>
  )
}

export default CustomTableContainer;