import { withStyles, makeStyles, Grid, Typography, Button, TextField } from '@material-ui/core';
import React from 'react';

const ContentGrid = withStyles({
  root: {
    paddingTop: 20,
    paddingBottom: 20
  }
})(Grid)

const useStyles = makeStyles({
  name: {
    fontSize: 40,
    fontWeight: 300,
    marginLeft: 30
  },
  walletAddress: {
    fontSize: 13,
    marginTop: 10
  },
  description: {
    fontSize: 13,
    marginTop: 20
  },
  graphContent: {
    marginTop: 20
  },
  graphTitle: {
    display: 'flex',
    justifyContent: 'center',
    fontSize: 22,
    fontWeight: 300
  },
  descriptionContent: {
    paddingTop: 20
  },
  bscitem: {
    marginTop: 20
  },
  bscDescription: {
    fontSize: 13,
    fontWeight: 300,
    marginLeft: 20
  },
  rateTitle: {
    fontSize: 16,
    fontWeight: 500,
    width: '100%',
    textAlign: 'center'
  },
  rateResponse: {
    fontSize: 14,
    fontWeight: 300,
    width: '100%',
    textAlign: 'center'
  },
  btnRateItem: {
    marginLeft: 20,
    marginRight: 20,
    width: 100,
    border: '1px solid #ddd',
    background: 'transparent',
    fontSize: 11,
    fontWeight: 300
  },
  chatitem: {
    marginTop: 20
  },
  chatContainer: {
    width: 500,
    marginTop: 100
  },
  chatText: {
    marginLeft: 10
  },
  chatUserName: {
    fontSize: 14,
    fontWeight: 500
  },
  chatDate: {
    fontSize: 13,
    fontWeight: 300,
    marginLeft: 10
  },
  chatContent: {
    marginTop: 5,
    fontSize: 14,
    fontWeight: 300
  },
  chatForm: {
    marginTop: 50,
    '& .input-form' : {
      marginLeft: 20, 
      marginRight: 20,
      width: 300
    }
  }
})

const BSCItem = () => {
  const classes = useStyles();

  return (
    <Grid container alignItems="center" className={classes.bscitem}>
      <img src="/assets/images/bsc.png" alt="Icon" width={45}/>
      <Typography component="span" className={classes.bscDescription}>View Contract on BSC</Typography>
    </Grid>
  )
}

const ChatItem = () => {
  const classes = useStyles();

  return (
    <Grid container className={classes.chatitem}>
      <Grid item>
        <img src="/assets/images/avatar1.png" alt="Icon" width={30}/>
      </Grid>
      <Grid item className={classes.chatText}>
        <Grid container>
          <Typography component="span" className={classes.chatUserName}>DogeFather</Typography>
          <Typography component="span" className={classes.chatDate}>a day ago</Typography>
        </Grid>
        <Grid container className={classes.chatContent}>
          Its te real!
        </Grid>
      </Grid>
    </Grid>
  )
}

const ChatForm = () => {
  const classes = useStyles();

  return (
    <Grid container className={classes.chatForm} alignItems="flex-end">
      <img src="/assets/images/avatar1.png" alt="Avatar" width={30} height={30}/>
      <TextField id="standard-basic" label="What do you think about this coin" className={'input-form'}/>
      <Button variant="contained" color="primary">Submit</Button>
    </Grid>
  )
}

const Detail = () => {
  const classes = useStyles();

  return (
    <ContentGrid container>
      <Grid item xs={6}>
        <Grid container alignItems="center">
          <Grid item xs={6}>
            <Grid container alignItems="center">
              <img src="/assets/images/avatar1.png" alt="Avatar" width={68}/>
              <Typography component="span" className={classes.name}>DogeFather</Typography>
            </Grid> 
          </Grid>
          <Grid item xs={6}>
            <Grid container alignItems="center">
              <Button><img src="/assets/images/send.png" alt="Icon" width={49}/></Button>
              <Button><img src="/assets/images/avatar4.png" alt="Icon" width={49}/></Button>
              <Button><img src="/assets/images/keymo.png" alt="Icon" width={49}/></Button>
            </Grid>
          </Grid>
        </Grid>
        <Grid container alignItems="center">
          <Typography component="span" className={classes.walletAddress}>0x3d29aa78fb558f84112bbc48a84f371147a920c9</Typography>
        </Grid>
        <Grid container className={classes.graphContent}>
          <Grid item xs={6}>
            <Typography className={classes.graphTitle}>Market Cap</Typography>
          </Grid>
          <Grid item xs={6} className={classes.descriptionContent}>
            <Typography className={classes.description}>
              A new deflationary token based on the latest u/elonmusk and u/binance tweet now live on Binance Smart Chain! ‚≠êÔ∏è10 Hours old ‚≠êÔ∏èMarket Cap: 6M ‚≠êÔ∏è6k Holders üî∏Owner is renounced üî∏Very active Telegram voice chat! üî∏5% distributed üî∏5% added to liquidity pool üî∏Dogefather have planned to develop an NFT project in the future with charity donation for each transaction.
            </Typography>
          </Grid>
        </Grid>

        <Grid container className={classes.graphContent}>
          <Grid item xs={6}>
            <Typography className={classes.graphTitle}>Holder Growth</Typography>
          </Grid>
          <Grid item xs={6} className={classes.descriptionContent}>
            <BSCItem />
            <BSCItem />
            <BSCItem />
          </Grid>
        </Grid>
      </Grid>

      {/* Chat Part */}
      <Grid item xs={6}>
        {/* Rating Component */}
        <Grid container justify="center">
          <Typography className={classes.rateTitle}>What do you think?</Typography>
          <Typography className={classes.rateResponse}>95 responses</Typography>
          <Grid container style={{marginTop: 20}} justify="center">
            <Button className={classes.btnRateItem}>Upvote</Button>
            <Button className={classes.btnRateItem}>Funny</Button>
            <Button className={classes.btnRateItem}>Love</Button>
          </Grid>
          <Grid container style={{marginTop: 20}} justify="center">
            <Button className={classes.btnRateItem}>Upvote</Button>
            <Button className={classes.btnRateItem}>Funny</Button>
            <Button className={classes.btnRateItem}>Love</Button>
          </Grid>
        </Grid>
        {/* Chat Component */}
        <Grid container justify="center">
          <Grid className={classes.chatContainer}>
            <ChatItem />
            <ChatItem />
            <ChatItem />
            <ChatItem />
            <ChatItem />
            <ChatForm />
          </Grid>
        </Grid>
      </Grid>
    </ContentGrid>
  )
}

export default Detail;