import React from 'react';
import { withStyles, makeStyles, Grid, Typography, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper } from '@material-ui/core';

const TableContainerGrid = withStyles({
  root: {
    padding: 20,
    borderRadius: 10,
  }
})(Grid)

const TablePaper = withStyles({
  root: {
    background: 'transparent',
    boxShadow: 'none',
  }
})(Paper)

const useStyles = makeStyles({
  table: {
  },
  borderNone: {
    border: 'none',
    textAlign: 'center',
  },
  flexCenter: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  coinLink: {
    marginTop: 10,
    fontSize: 12
  },
  btnCnt: {
    background: 'green',
    color: 'white',
    width: '140px',
    justifyContent: 'flex-start',
    paddingLeft: 20,
    '&:hover': {
      background: '#0080009e',
    },
    '& span span': {
      marginLeft: 15
    }
  }
});

const MyCoinsTableContainer = ({colorBackground, rows, spacing = {}}) => {
  const classes = useStyles();

  return (
    <React.Fragment>
      <TableContainerGrid container style={{background: colorBackground, marginTop: spacing !== {} ? spacing : 0, flexWrap: 'nowrap'}}>
        <TableContainer component={TablePaper}>
          <Grid container>
            <Grid container item xs={6} alignItems="center">
              <img src={'/assets/images/monkey.png'} width={31}  alt="Monkey"/>
              <Typography>
                My Coins
              </Typography>
            </Grid>
          </Grid>
          <Table className={classes.table} aria-label="simple table">
            <TableHead className={classes.borderNone}>
              <TableRow  className={classes.borderNone}>
                <TableCell className={classes.borderNone}></TableCell>
                <TableCell className={classes.borderNone}></TableCell>
                <TableCell align="right" className={classes.borderNone}>Market Cap</TableCell>
              </TableRow>
            </TableHead>
            <TableBody className={classes.borderNone}>
              {rows.map((row, key) => (
                <TableRow key={key} className={classes.borderNone}>
                  <TableCell component="th" scope="row" className={`${classes.flexCenter} ${classes.borderNone}`}>
                    <img width={36} src={row.avatar}  alt="Avatar"/>
                  </TableCell>
                  <TableCell align="right" className={classes.borderNone}>{row.name}</TableCell>
                  <TableCell align="right" className={classes.borderNone}>{row.marketCap}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </TableContainerGrid>
    </React.Fragment>
  )
}

export default MyCoinsTableContainer;