import { withStyles, Grid } from '@material-ui/core';
import CustomTableContainer from '../components/TableContainer'
import PopularTableContainer from '../components/PopularTable'
import MyCoinsTableContainer from '../components/MyCoinsTableContainer'

const ContentGrid = withStyles({
  root: {
    paddingTop: 20,
    paddingBottom: 20
  }
})(Grid)

function createData(image, calories, fat, carbs, imgSend) {
  return { image, calories, fat, carbs, imgSend };
}

const rows = [
  createData('/assets/images/avatar1.png', 'DogeFather', '11,506', '$15,236,342', '/assets/images/send.png'),
  createData('/assets/images/avatar2.png', 'ElonTesla', '5,506', '$15,236,342', '/assets/images/send.png'),
  createData('/assets/images/avatar3.png', 'SpaceBunny', '2,506', '$15,236,342', '/assets/images/send.png'),
];

function createPopularData(avatar, name, marketCap, orderImg, oldDays, holders, cnt) {
  return { avatar, name, marketCap, orderImg, oldDays, holders, cnt };
}

const popularRows = [
  createPopularData('/assets/images/avatar4.png', 'Hyper Meteor', '$15,236,342', '/assets/images/arrow-down.png', 1, '11,506', 1003),
  createPopularData('/assets/images/avatar4.png', 'ZeppelinDAO', '$15,236,342', '/assets/images/arrow-up.png', 2, '5,506', 948),
  createPopularData('/assets/images/avatar4.png', 'SafeCaw', '$15,236,342', '/assets/images/arrow-down.png', 15, '2,506', 489),
  createPopularData('/assets/images/avatar4.png', 'SafeCaw', '$15,236,342', '/assets/images/arrow-down.png', 15, '2,506', 489),
  createPopularData('/assets/images/avatar4.png', 'SafeCaw', '$15,236,342', '/assets/images/arrow-down.png', 15, '2,506', 489),
  createPopularData('/assets/images/avatar4.png', 'SafeCaw', '$15,236,342', '/assets/images/arrow-down.png', 15, '2,506', 489),
  createPopularData('/assets/images/avatar4.png', 'SafeCaw', '$15,236,342', '/assets/images/arrow-down.png', 15, '2,506', 489),
  createPopularData('/assets/images/avatar4.png', 'SafeCaw', '$15,236,342', '/assets/images/arrow-down.png', 15, '2,506', 489),
  createPopularData('/assets/images/avatar4.png', 'SafeCaw', '$15,236,342', '/assets/images/arrow-down.png', 15, '2,506', 489),
];

function createMyData(avatar, name, marketCap) {
  return { avatar, name, marketCap};
}

const myCoinsRows = [
  createMyData('/assets/images/avatar4.png', 'Hyper Meteor', '$15,236,342'),
  createMyData('/assets/images/avatar4.png', 'Hyper Meteor', '$15,236,342'),
  createMyData('/assets/images/avatar4.png', 'Hyper Meteor', '$15,236,342'),
  createMyData('/assets/images/avatar4.png', 'Hyper Meteor', '$15,236,342'),
  createMyData('/assets/images/avatar4.png', 'Hyper Meteor', '$15,236,342'),
  createMyData('/assets/images/avatar4.png', 'Hyper Meteor', '$15,236,342'),
];

const Dashboard = () => {
  return (
    <ContentGrid container>
      <Grid container item xs={4}>
        <CustomTableContainer title={'Featured BSC Coins'} colorBackground={'linear-gradient(180deg, #E6FBF5, #F7FDFB)'} rows={rows} />
        <CustomTableContainer title={'Upcoming Featured Presales'} colorBackground={'linear-gradient(180deg, #FFFAEF, #FFFEFD)'} rows={rows} spacing={20}/>
      </Grid>
      <Grid container item xs={8} style={{paddingRight: 20, paddingLeft: 20}}>
        <Grid container>
          <PopularTableContainer colorBackground={'transparent'} rows={popularRows}/>
        </Grid>
        <Grid container item xs={6}>
          <MyCoinsTableContainer colorBackground={'transparent'} rows={myCoinsRows}/>
        </Grid>
      </Grid>
    </ContentGrid>
  )
}

export default Dashboard;